.hero{
    min-height: 50vh;
    display: flex;
    justify-content: center;
    position: relative;
    z-index: 100;
}
.hero_div {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 90%;
}
.hero-container{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    padding-top: 100px;
    padding-bottom: 100px;
    width: fit-content;
}

#badges a  {
    margin-right: 10px;
}

.hero-container > img {
    object-fit: cover;
    width: 176px;
    height: 176px;
    margin-top: 70px;
    margin-bottom: 30px;
    border-radius: 50%;
}

.hero-container__button {
    font-family: 'Roboto';
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    text-align: center;
    letter-spacing: -0.011em;
    width: 116px;
    height: 26px;
    background: #11468F;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    border-radius: 7px;
    color: #F7F5F2;
    border: none;
    cursor: pointer;
    margin-top: 30px;
}

.icons > img {
    width: 40px;
    padding: 10px;
    cursor: pointer;
}

@media (max-width: 400px) {
    .hero_div {
        display: flex;
        flex-direction: column;
        justify-content: center;
    }

    .hero_div  svg {
        width: 200px;
        height: 200px;
    }
    .hero-container {
        padding: 0;
    }
}